
import VHeader from '@/components/v-header/v-header'
import SearchHeader from '@/components/seach-header/seach-header'
import Scroll from '@/components/scroll/scroll'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'


import animations from 'create-keyframe-animation'
import { isIphoneX } from '@/assets/js/brower'
import { localUser } from '@/assets/js/local'
import axios from '@/api/axiosApi'
import defaultAvatar from '@/assets/img/default_avatar.png'

export default {
name: 'strategyList',
data() {
return {
showSearch: false,
showSelectType: false,
defaultAvatar,
seachValue: '',
searchList: [],
travelsList: []
}
},
directives: {
TransferDom
},
components: {
VHeader, Scroll, Popup, SearchHeader
},
created() {
this.paddingTop = 20
this.paddingBottom = 0
if (isIphoneX()) {
this.paddingTop = 34
this.paddingBottom = 20
}
this.pageNum = 1

animations.registerAnimation({
name: 'rotateOut',
animation: [
{
transform: 'translate(-50%, 0)'
},
{
transform: 'translate(-50%, ' + (this.paddingBottom + 100) + 'px)'
}
],
presets: {
duration: 800,
resetWhenDone: true
}
})
animations.registerAnimation({
name: 'rotateIn',
animation: [
{
transform: 'translate(-50%, ' + (this.paddingBottom + 100) + 'px)'
},
{
transform: 'translate(-50%, 0)'
}
],
presets: {
duration: 800,
resetWhenDone: true
}
})

this._getPlayStrategList()
},
mounted () {
this.$refs.scroll.$el.style.height = window.innerHeight - this.$refs.header.$el.clientHeight + 'px'
},
methods: {
_getPlayStrategList() {
const _self = this
axios.get(this, '/v1/playStrategy', {
pageNum: this.pageNum
}, (data) => {
console.log(data)
if (data.length === 0) {
if (_self.pageNum > 0) {
if (_self.$refs.scroll) {
_self.pageNum--
_self.$refs.scroll.hiddenPullUpLoad()
}
} else {
_self.$refs.scroll.hiddenPullDown()
}
return
}
if (_self.pageNum <= 1) {
_self.travelsList = data
} else {
_self.travelsList = _self.travelsList.concat(data)
}
})
},
scroll(position, directionY) {
if (this.showPlus && directionY > 0) {
animations.runAnimation(this.$refs.plus, 'rotateOut', () => {
this.showPlus = false
})
} else if (!this.showPlus && directionY < 0) {
animations.runAnimation(this.$refs.plus, 'rotateIn', () => {
this.showPlus = true
})
}
},
onPullingDown() {
this.pageNum = 1
this._getPlayStrategList()
},
onPullingUp() {
this.pageNum++
this._getPlayStrategList()
},
toDetail(id) {
this.$router.push(`/address/playStrategy/${id}`)
},
submitSearch(value) {
axios.get(this, '/v1/playStrategy', {
title: value
}, (data) => {
this.searchList = data
})
},
clickSearchItem(item) {
this.toDetail(item.id)
this.showSearch = false
this.seachValue = ''
this.searchList = []
},
sendDynamic (type) {
const user = localUser.get()
if (user == null) {
this.$router.push('/user/login')
return
}
this.showSelectType = false
this.$router.push(`/address/travelsEdit/${type}`)
}
}
}



